

.col-md-12{
    padding: 68px 145px 80px;
}


.col-md-12 .middle_section .top_follow{
    display: flex;
    align-items: center;
    padding-bottom: 20px;
}

.col-md-12 .middle_section .top_follow  svg{
    margin-left: 10px;
}

.col-md-12 .middle_section .top_follow .follow_all{
   margin-top: 26px;
}

.col-md-12 .middle_section .top_follow .follow_all i{
    position: relative;
    bottom: -0.5px;
    padding: 0px 5px;
    transform: scale(0.2);
    -webkit-transform: scale(0.2);
    -moz-transform: scale(0.2);
    -ms-transform: scale(0.2);
    -o-transform: scale(0.2);
}

.col-md-12 .middle_section .top_follow  .file svg,.col-md-12 .middle_section .top_follow  .fa-plus{
    display: none ;
}

.col-md-12 .middle_section .top_follow .follow_name{
    font-size: 18px;
    font-weight: bold;
    line-height: 1.3;
}

.col-md-12 .middle_section .top_follow .owner{
    font-size: 15px;
    font-weight: 400;
}



.col-md-12 .middle_section .image_section{
    margin-right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    overflow: hidden;
}

.col-md-12 .middle_section .image_section img{
    width: 50%;
    object-fit: cover;
}

.col-md-12 .middle_section img{
    width: 100%;
}

.col-md-12 .middle_section .bottom_section{
    padding: 32px 45px 20px;
    background-color: #fafafa;
    border: solid 1px #e2e2e2;
}

.col-md-12 .middle_section .bottom_section .inner_section {
    padding: 40px 30px;
    border: solid 1px #e2e2e2;
}

.col-md-12 .middle_section .bottom_section .inner_section h1{
    font-size: 27px;
    font-weight: 900;
    line-height: 1;
    margin-bottom: 10px;
}

.col-md-12 .middle_section .bottom_section .inner_section p{
    font-size: 15px;
    font-weight: normal;
    line-height: 1.3;
}

.col-md-12 .middle_section .bottom_section .inner_section  .social_section{
    display: flex;
    align-items: center;
    gap: 10px;
}

.col-md-12 .middle_section .bottom_section .inner_section  .social_section button{
    background: #0057ff;
    border-color: transparent;
    color: #fff;
    padding: 4px 18px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}

.col-md-12 .middle_section .bottom_section .inner_section  .social_section .or{
    color: #a9a9a9;
}

.col-md-12 .middle_section .bottom_section .inner_section  .social_section a{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border: solid 1px #e2e2e2;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}


.col-md-12 .middle_section .bottom_section .inner_section .comment_items{
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.col-md-12 .middle_section .bottom_section .inner_section:nth-child(2){
    padding-bottom: 0px;
}

.col-md-12 .middle_section .bottom_section .inner_section .comment_items img{
    width: 50px;
    height: 50px;
    margin-right: 20px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.appri_section{
    display: flex;
    justify-content: center;
    padding: 50px 0px 30px;
    color: white;
    background-color: black;
}

.appri_section .like_div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    width: 75px;
    height: 75px;
    background-color:#0057ff;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.appri_section .like_div span{
    font-size: 10px;
    font-weight: bold;
    padding-top: 10px;
}

.appri_section .top_heading{
    font-size: 28px;
    font-weight: bold;
    line-height: 1.2;
    margin: 15px  0 10px;
    text-align: center;
}

.appri_section .like_comment_see_section{
    display: flex;
    gap: 8px;
    justify-content: center;
    align-items: center;
}

.appri_section .like_comment_see_section i,.appri_section .like_comment_see_section span{
    font-size: 14px;
    color: #959595;
}

.appri_section .publish{
    font-size: 14px;
    padding-top: 20px;
    font-weight: 300;
    color: #959595;
}

.slider_section{
    width: 100%;
    height: 500px;
    padding: 50px;
    background-color: #191919;
}

.slider_section .Boot_slider .slider_container{
    padding: 0px;
}

.slider .container .slide-box{
    display: flex;
    gap: 20px;
}

.slider_section .bottom_follow_section{
    display: flex;
    align-items: center;
}

.slider_section .bottom_follow_section .follow_img_section{
    width: 50px;
    height: 50px;
    background-color: white;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
}

.slider_section .bottom_follow_section .follow_img_section img{
    width: 50%;
}

.slider_section .bottom_follow_section a{
    display: block;
    text-decoration: none;
}

.slider_section .bottom_follow_section .multi_owner{
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    line-height: 1;
    position: relative;
}

.slider_section .bottom_follow_section .multi_owner .fa-sort-down{
    position: absolute;
    font-size: 15px;
    margin-left: 10px;
}

.slider_section .bottom_follow_section .follow_all_section{
    background-color: #0057ff;
    text-align: center;
    color: white;
    width: fit-content;
    font-size: 14px;
    margin-top: 7px;
    padding: 5px 15px;
    font-weight: 600;
    line-height: 1.25;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}




.col-md-12 .middle_section .bottom_section .inner_section .comment_items a{
    color: #3c3c3c;
    font-size: 14px;
    font-weight: bold;
}

.col-md-12 .middle_section .bottom_section .inner_section .comment_items a  span{
    color: #a9a9a9;
    font-size: 11px;
}

.col-md-12 .middle_section .bottom_section .inner_section .comment_items a  span::before{
    content: "";
    color: #a9a9a9;
    border-radius: 50%;
    font-size: 20px;
    margin: 5px;
}

.col-md-12 .middle_section .bottom_section .inner_section .comment_items .comment{
    color: #484848;
}

.col-md-12 .middle_section .bottom_section .inner_section .see_all_comments{
    color: #0057ff;
    cursor: pointer;
    font-size: 18px;
    height: 75px;
    line-height: 73px;
    text-align: center;
}

.col-md-12 .middle_section .two_section{
    padding-top: 0px;
    display: flex;
    gap: 12px;
}

.col-md-12 .middle_section .bottom_section .left_side,.col-md-12 .middle_section .bottom_section .right_section{
    width: 100%;
}

.col-md-12 .middle_section .bottom_section .left_section,.col-md-12 .middle_section .bottom_section .right_content{
    border: solid 1px #e2e2e2;
    width: 100%;
    padding: 27px 30px;
}

.col-md-12 .middle_section .bottom_section .left_section #owner{
    color: #707070;
    font-size: 11px;
    font-weight: 600;
    line-height: 1;
    text-transform: uppercase;
}


.col-md-12 .middle_section .bottom_section .left_section .follow_items,.col-md-12 .middle_section .bottom_section .left_bottom .follow_items{
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 15px;
}

.col-md-12 .middle_section .bottom_section .left_section .follow_items img,.col-md-12 .middle_section .bottom_section .left_bottom img{
    width: 40px;
    height: 40px;
    margin-right: 10px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.col-md-12 .middle_section .bottom_section .left_section .follow_items .follow_item_text .follow_name a,.col-md-12 .middle_section .bottom_section .left_bottom a{
    font-size: inherit;
    font-weight: bold;   
     white-space: nowrap;
     padding-bottom: 3px;
    color: #191919;
}

.col-md-12 .middle_section .bottom_section .left_section .follow_items .follow_item_text .city,.col-md-12 .middle_section .bottom_section .left_bottom .city{
    font-size: .9em;
    line-height: 1.2;
    color: dimgray;
}

.col-md-12 .middle_section .bottom_section .left_section .follow_items button{
    font-size: 12px;
    padding: 4px 15px;
    font-weight: 600;
    line-height: 1.25;
    border: 1px solid transparent;
    background-color: #0057ff;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    color: white;
    position: absolute;
    right: 0px;
}

.col-md-12 .middle_section .bottom_section .left_bottom{
    position: relative;
    margin-top: 20px;
    border: solid 1px #e2e2e2;
    width: 100%;
    padding: 27px 30px;
}

.col-md-12 .middle_section .bottom_section .left_bottom p{
    color: #707070;
    font-size: 11px;
    font-weight: 600;
    line-height: 1;
    text-transform: uppercase;
}




/* right section ********************************************************* */

.col-md-12 .middle_section .bottom_section .right_section .right_content{
    margin-bottom: 20px;
    border: solid 1px #e2e2e2;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content h1{
    white-space: nowrap;
    color: #191919;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 600;
    padding-bottom: 14px;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content p{
    font-size: 14px;
    margin-bottom: 22px;
    word-wrap: break-word;
    color: dimgray;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content .icon span{
    display: inline-block;
    font-size: 14px;
    height: 17px;
    line-height: 17px;
    margin-right: 9px;
    color: dimgray;
    padding-bottom: 35px;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content span{
    color: dimgray;
    font-size: 12px;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content .heading{
    color: #707070;
    font-size: 11px;
    font-weight: 600;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 15px;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content .img_background{
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    padding: 10px 30px 12px 30px;
    min-height: 47px;
    margin-bottom: 8px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#fst{
    background-image: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)), url("https://mir-s3-cdn-cf.behance.net/projects/max_808/0023fc100352159.Y3JvcCwyMTgyLDE3MDcsMCww.jpg");
}

#snd{
    background-image: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)), url("https://mir-s3-cdn-cf.behance.net/projects/max_808/ecbf5a81379237.Y3JvcCw4MDgsNjMyLDAsMA.jpg");
}

#thrd{
    background-image: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)), url("https://mir-s3-cdn-cf.behance.net/projects/max_808/734878126387305.Y3JvcCwxNjE2LDEyNjQsMCww.png");
}

#frt{
    background-image: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)), url("https://a5.behance.net/a3fd36a13353a9f95a0f9faed22a7ccf1cf0170e/img/creative_fields/bg/webdesign.jpg");
}

#fif{
    background-image: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)), url("https://mir-s3-cdn-cf.behance.net/projects/404/0843b8126693217.Y3JvcCwyMTI3LDE2NjQsMjA1LDA.png");
}

#six{
    background-image: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)), url("https://a5.behance.net/a3fd36a13353a9f95a0f9faed22a7ccf1cf0170e/img/creative_fields/bg/interactiondesign.jpg");
}

.col-md-12 .middle_section .bottom_section .right_section .right_content .bg_1{
    background-image: url();
}

.col-md-12 .middle_section .bottom_section .right_section .right_content .img_background img{
    width: 25px;
    height: 25px;
    margin-right: 5px;
}

.col-md-12 .middle_section .bottom_section .right_section .right_content .img_background p{
    font-size: 18px;
    font-weight: 900;
    margin-bottom: auto;
    margin-left: 5px;
    margin-top: auto;
    white-space: nowrap;
    color: white;
}

.col-md-12 .middle_section .bottom_section .right_content h2{
    color: #191919;
    font-size: 16px;
    font-weight: bold;
    padding: 15px 0px 30px;
}

.col-md-12 .middle_section .bottom_section .right_content .tags{
    display: inline-block;
    padding: 8px 22px;
    border: solid 1px #e2e2e2;
    margin: 8px 5px; 
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    text-decoration: none;
    color: #191919;
}

.col-md-12 .middle_section .bottom_section .copyright_section{
    display: flex;
    position: relative;
}

.col-md-12 .middle_section .bottom_section .copyright_section p{
    color: #959595;
    font-size: 11px;
    font-weight: 600;
    line-height: 1.5;
}

.col-md-12 .middle_section .bottom_section .copyright_section .report{
    position: absolute;
    right: 0;
}


/* media queries **************************************************8*/


@media screen and (max-width: 1080px) {
    .col-md-12{
        padding: 80px 110px 80px;
    }
    .col-md-12 .middle_section{
        padding-top: 60px;
    }
}


@media screen and (max-width: 1024px) {
    .col-md-12{
        padding: 0px 0px 80px;
    }
    .col-md-12 .middle_section{
        border: none;
    }
    .col-md-12 .middle_section .top_follow{
        padding: 15px 20px 20px;
        position: relative;
    }
    .col-md-12 .middle_section .top_follow .file svg{
        display: block;
        position: absolute;
        top: 25px;
        right: 130px;
        border: solid 1px #e4e4e4;
        width: 30px;
        height: 30px;
        padding: 8px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
}
    .col-md-12 .middle_section .top_follow  i{
        display: none ;
    }
    .col-md-12 .middle_section .top_follow .follow_all{
        position: absolute;
        right: 0px;
        padding: 5px 20px;
        margin: auto 20px;
        background-color: #0057ff;
        border-radius: 50px;
        -webkit-border-radius: 50px;
        -moz-border-radius: 50px;
        -ms-border-radius: 50px;
        -o-border-radius: 50px;
    }
    .col-md-12 .middle_section .top_follow .follow_all a{
        color: white;
        font-size: 15px;
        font-weight: 600;
    }
    .col-md-12 .middle_section .bottom_section{
        padding: 32px 45px 20px;
        background-color: #fafafa;
    }
    .appri_section{
        display: none;
    }
}

@media screen and (max-width: 922px) {
    .col-md-12 .middle_section .bottom_section{
        padding: 32px 40px 20px;
    }
    .col-md-12 .middle_section .bottom_section .right_section .right_content .img_background{
        display: inline-block;
        margin-right: 8px;
    }
    .col-md-12 .middle_section .bottom_section .right_section .right_content .img_background img,.col-md-12 .middle_section .bottom_section .right_section .right_content .img_background p{
        display: inline-block;
    }
}

@media screen and (max-width: 604px) {
    .col-md-12 .middle_section .bottom_section{
        padding: 100px 20px 0px;
    }
    .col-md-12 .middle_section .two_section{
        display: block;
        padding: 20px 20px 75px;
    }
    .col-md-12 .middle_section .bottom_section .left_side{
        margin-bottom: 20px;
    }
    .col-md-12 .middle_section .bottom_section .inner_section {
        padding: 40px 30px;
    }
    .col-md-12 .middle_section .top_follow .follow_all{
        display: none;
    }
    .col-md-12 .middle_section .top_follow .file svg{
        right: 75px;
    }
    .col-md-12 .middle_section .top_follow .plus .fa-plus{
        display: block;
        position: absolute;
        top: 30px;
        right: 30px;
        font-size: 18px;
        color: #191919;
    }
    .col-md-12 .middle_section .bottom_section .inner_section  .social_section button span{
        display: none !important;
    }
}